import React from "react";
import { makeStyles } from "@material-ui/styles";
import {
  Box,
  Grid,
  Card,
  CardActionArea,
  CardContent,
  CardActions,
  CardMedia,
  Button,
  Typography,
} from "@material-ui/core";
import Navbar from "./Navbar";
import TechReach from "../images/TechReach.jpg";
import CGPA from "../images/CGPA.jpg";
import MyPortfolio from "../images/MyPortfolio.jpg";
import FirebaseLogin from "../images/FirebaseLogin.jpg";
import ProjectN from "../images/ProjectN.png";

const myStyles = makeStyles({
  mainContainer: {
    backgroundColor: "white",
    // background: 'skyBlue',
    height: "100%",
    // position: 'rlative',
  },
  cardContainer: {
    maxWidth: "345",
    // margin: '3rem',
    margin: "1rem 2.5rem 5rem 2.5rem",
    backgroundColor: "skyBlue",
    color: "navy",
    textAlign: "justify",
    textAlign: "center",
  },
});

export const Portfolio = () => {
  const classes = myStyles();
  return (
    <Box component="div" className={classes.mainContainer}>
      <Navbar />
      <Grid container justify="center">
        {/* CGPA PROJECT */}
        <Grid item xs={12} sm={6} md={4}>
          <Card className={classes.cardContainer}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={CGPA} />

              <CardContent>
                <Typography gutterBottom variant="h4">
                  CGPA Calculator
                </Typography>

                <Typography
                  style={{ color: "navy", textAlign: "justify" }}
                  variant="body"
                  color="textSecondary"
                  component="p"
                >
                  <br /> <br />
                  REACT | CSS
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              {/* <Button size='large' color='navy'>
                                    Share
                                </Button> */}
              <a
                style={{ textDecorationLine: "none" }}
                href="https://calcugp.netlify.app"
                target="_blank"
              >
                {" "}
                <Button size="large" color="navy">
                  Visit
                </Button>
              </a>
            </CardActions>
          </Card>
        </Grid>

        {/* MY PORTFOLIO PROJECT  */}
        <Grid item xs={12} sm={6} md={4}>
          <Card className={classes.cardContainer}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={MyPortfolio} />

              <CardContent>
                <Typography gutterBottom variant="h4">
                  My Portfolio
                </Typography>

                <Typography
                  style={{ color: "navy", textAlign: "justify" }}
                  variant="body"
                  color="textSecondary"
                  component="p"
                >
                  <br /> <br />
                  ReactJS | Material UI | CSS
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              {/* <Button size='large' color='navy'>
                                    Share
                                </Button> */}
              <a
                style={{ textDecorationLine: "none" }}
                href="https://geniushawlah.github.io"
                target="_blank"
              >
                {" "}
                <Button size="large" color="navy">
                  Visit (Still here though)
                </Button>
              </a>
            </CardActions>
          </Card>
        </Grid>

        {/* TECHREACH PROJECT */}
        <Grid item xs={12} sm={6} md={4}>
          <Card className={classes.cardContainer}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={TechReach} />

              <CardContent>
                <Typography gutterBottom variant="h4">
                  TechReach
                </Typography>

                <Typography
                  style={{ color: "navy", textAlign: "justify" }}
                  variant="body"
                  color="textSecondary"
                  component="p"
                >
                  <br /> <br />
                  HTML | CSS | JS | BOOTSTRAP
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              {/* <Button size='large' color='navy'>
                                    Share
                                </Button> */}
              <a
                style={{ textDecorationLine: "none" }}
                href="https://techreach.netlify.app"
                target="_blank"
              >
                {" "}
                <Button size="large" color="navy">
                  Visit
                </Button>
              </a>
            </CardActions>
          </Card>
        </Grid>

        {/* FIREBASE LOGIN PROJECT  */}
        <Grid item xs={12} sm={6} md={4}>
          <Card className={classes.cardContainer}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={FirebaseLogin} />

              <CardContent>
                <Typography gutterBottom variant="h4">
                  Firebase Desktop Member Page
                </Typography>
                <Typography
                  style={{ color: "navy", textAlign: "justify" }}
                  variant="body"
                  color="textSecondary"
                  component="p"
                >
                  <br /> <br />
                  ReactJS | CSS | Firebase
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <a
                style={{ textDecorationLine: "none" }}
                href="https://memberlogin.netlify.app/"
                target="_blank"
              >
                {" "}
                <Button size="large" color="navy">
                  Visit
                </Button>
              </a>
            </CardActions>
          </Card>
        </Grid>
        {/* PROJECT FOUR */}
        <Grid item xs={12} sm={6} md={4}>
          <Card className={classes.cardContainer}>
            <CardActionArea>
              <CardMedia component="img" height="140" image={ProjectN} />

              <CardContent>
                <Typography gutterBottom variant="h4">
                  Project Four
                </Typography>
                <Typography
                  style={{ color: "navy", textAlign: "justify" }}
                  variant="body"
                  color="textSecondary"
                  component="p"
                >
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Commodi nulla aspernatur harum deleniti iure suscipit
                  excepturi id? Consequuntur, ut veritatis!
                </Typography>
              </CardContent>
            </CardActionArea>
            <CardActions>
              <Button size="large" color="navy">
                Share
              </Button>
              <Button size="large" color="navy">
                Live Demo
              </Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};
