import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import MobileRightMenuSlider from '@material-ui/core/Drawer';
import { AppBar, Toolbar, ListItem, IconButton, ListItemText, Avatar, Divider, List, Typography, Box, ListItemIcon } from '@material-ui/core';
import {  Menu, Web, AssignmentInd, Description, Home, Apps, ContactMail } from '@material-ui/icons';
import profilePic from '../profilePic.jpg';


//! MY CSS STYLES USING THE IMPORTED makeStyles
const myStyle = makeStyles(theme => ({
    // STYLE FOR THE MENU SLIDER
    menuSliderContainer: {
        width: 250,
        backgroundColor: 'skyBlue',
        height: '100%'
    },

    // STYLE FOR THE PROFILE PIC
    profilePic: {
        display: 'block',
        margin: '0.5rem auto',
        width: theme.spacing(13),
        height: theme.spacing(13)
    },

    // STYLE FOR THE ICONS/LABELS UNDER PROFILE PIC
    menuItemStyle: {
        color: 'navy'

    }

}));
// THIS IS AN ARRAY OF OBJECTS (ICONS AND THE TEXTS) UNDER THE PROFILE PIC. IT WILL BE MAPPED THROUGH IN THE return SCOPE WHEN NEEDED
const menuItems = [
    {
        listIcon: <Home />,
        listText: 'Home',
        listPath: '/',
    },
    {
        listIcon: <AssignmentInd />,
        listText: 'Resume',
        listPath: '/resume',
        click: onClick=>{alert("You want to view my journey so far? It's actually some boring stories.")}

    },
    {
        listIcon: <Apps />,
        listText: 'Portfolio',
        listPath: '/portfolio'
    },
    // {
    //     listIcon: <ContactMail />,
    //     listText: 'Contacts',
        // listPath: '/contacts'
    //     click: onClick=>{alert('Page under construction')}

    // },

    {
        listIcon: <Web />,
        listText: 'Blog',
        // listPath: '/blog'
        click: onClick=>{alert('Page under construction')}

    },
{
        listIcon: <Description />,
        listText: 'Articles',
        // listPath: '/articles'
        click: onClick=>{alert('Page under construction')}

    },

]


const Navbar = () => {
    //  CREATING THE STATE FOR THE MENUBAR/MENUSLIDER
    const [menuBarState, setMenuBarState] = useState({
        right: false // INITIAL VALUE OF menuBarState
    });

    // SETTING MENUBAR TOGGLER (ASSIGNING THE toggleMenuBar FUNCTION TO THE setMenuBarState)
    const toggleMenuBar = ((slider, open) => () => {
        setMenuBarState({ ...menuBarState, [slider]: open });
    });

    const classes = myStyle(); //SO I CAN MANIPULATE THE DEFAULT STYLES

    const menuSlider = (slider) => (
        //  THE PROFILE PIC 
        <Box
            className={classes.menuSliderContainer}
            component='div'
        onClick={toggleMenuBar(slider, false)}
        >
            <Avatar className={classes.profilePic} src={profilePic} />
            <Divider />
            {/* THE LIST AND TEXTS/LABELS */}
            <List>
                
                {/* THE MAP FUNCTION TAKES A FUNCTION THAT HAS THE PARAMETERS IsItem(EACH ITEM IN THE ARRAY) AND THE key (INDEX) */}
                {menuItems.map((IsItem, key) => (
                    
                    <ListItem button /* THE button IS THERE FOR HOVER EFFECT */ key={key}
                       onClick={IsItem.click}
                        component={Link} to={IsItem.listPath} 
                     >
                        
                        <ListItemIcon className={classes.menuItemStyle}>
                            {IsItem.listIcon} {/* THE ICONS */}
                        </ListItemIcon>
                        <ListItemText className={classes.menuItemStyle} primary={IsItem.listText} /> {/*THE TEXTS/LABELS */}
                    </ListItem>

                ))}

            </List>
        </Box>
    )

    return (
        <>

            <Box component='nav' style={{ margin: '0rem 0rem 5.3rem 0rem', padding: '0rem 0rem 0.5rem 0rem', }} >
                <AppBar  style={{ backgroundColor: 'skyblue' }}>
                    <Toolbar position='fixed'>
                        <IconButton onClick={toggleMenuBar('right', true)}>
                            <Menu style={{ color: 'navy' }}/>
                        </IconButton>

                        <Typography variant='h5' style={{ color: 'navy',   }}>
                            More
                    </Typography>
                        <MobileRightMenuSlider  open={menuBarState.right} onClose={toggleMenuBar('right', false)}>
                            {menuSlider('right')}
                            

                        </MobileRightMenuSlider>
                    </Toolbar>
                </AppBar>
            </Box>

        </>
    )
}
export default Navbar;