import React from 'react'
import { Link } from 'react-router-dom';

import { Button, ButtonGroup, BottomNavigationAction, useMediaQuery, Typography, Avatar, Grid, Box, } from '@material-ui/core'
import { Facebook, Twitter, LinkedIn, Instagram, GitHub, Email, WhatsApp } from '@material-ui/icons';

import { makeStyles, useTheme } from '@material-ui/core/styles'
import Typed from 'react-typed'
import profilePic from '../profilePic.jpg';

//! TO APPLY CSS STYLING
const myStyles = makeStyles(theme => ({

    profilePic: {
        width: theme.spacing(20),
        height: theme.spacing(20),
        margin: theme.spacing(1)
    },
    titleAndSubtitle: {
        color: 'skyBlue'
    },
    theWholeBox: {
        position: "absolute",
        top: '25%',
        left: 'translate( 50%, -50%)',
        width: '100%',
        textAlign: 'center',
        zIndex: '1'
    },
  
    button: {
        // flexWrap: 'wrap',
        display: 'flex',
        marginTop: '1rem',
        color: 'skyBlue',
        // borderColor: 'skyBlue',
        // backgroundColor: 'white',
        "&:hover": {
            // background: 'skyBlue',
            fill: 'navy',

        },
    },

    root: {
         fontWeight: theme.typography.fontWeightBold,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        '& > *': {
            margin: theme.spacing(1),
        },
        //   MANIPULATION FOR THE ICONS
        '& .MuiBottomNavigationAction-root': {
            minWidth: 0,
            maxWidth: 250,
        },
        '& .MuiSvgIcon-root': {
            fill: 'skyblue',
// outline:'skyblue',
            fontSize: '2rem',
            "&:hover": {
                fill: 'white',
            },
        },
           
    },


}));


export const Header = () => {


    const classes = myStyles();
    

    return (
        <Box style={{ display: 'flex', flexDirection: 'column', position: 'fixed',  height:'auto', width:'100%' }} className={classes.theWholeBox}>
            <Grid container justify='center'>
                <Avatar className={classes.profilePic} src={profilePic} />
            </Grid>
            {/* MY NAME */}
            
            <Typography className={`${classes.titleAndSubtitle} ${classes.root}`}  variant='h6' >
                Olasunkanmi AJIBOLA
            </Typography>
            {/* <br /> */}
            <Typography className={`${classes.titleAndSubtitle} ${classes.root}`} variant='subtitle' className={classes.titleAndSubtitle}>I am a rapid-growing Software Developer who is passionate about solving problems with technology. Specifically,... 
            </Typography>
            <br/>
            {/* WHAT I DO */}
            <Typography className={classes.titleAndSubtitle} variant='body2' >
                <Typed strings={['I Develop Web &amp; Mobile Apps.', 'I Write Fiction &amp; Non-fiction Contents.', 'I Design Fliers, &amp; Other Printables.']} typeSpeed='8' cursorChar='' loop='true' smartBackspace='true' backDelay='5000' />
            </Typography>

            {/* SOCIAL MEDIA ICONS */}
            <div style={{marginTop:'-1rem', marginBottom:'-0.5rem'}} className={classes.root}>
                <ButtonGroup size="small" color="" aria-label="small outlined primary button group">

                    <a href='https://twitter.com/GeniusHawlah' target="_blank">
                        <Button className={classes.button}>
                            <BottomNavigationAction className={classes.root}
                                style={{ padding: 0 }}
                                icon={<Twitter />}
                            />
                        </Button>
                    </a>
                    <a href='https://www.facebook.com/GeniusHawlah' target="_blank">
                        <Button className={classes.button}>
                            <BottomNavigationAction className={classes.root}
                                style={{ padding: 0 }}
                                icon={<Facebook />} />
                        </Button>
                    </a>

                    <a href='https://linkedin.com/in/olasunkanmi-ajibola-534759177' target="_blank">
                        <Button className={classes.button}>
                            <BottomNavigationAction className={classes.root}
                                style={{ padding: 0 }}
                                icon={<LinkedIn />}
                            />
                        </Button>
                    </a>



                    <a href='https://github.com/GeniusHawlah' target="_blank">
                        <Button className={classes.button}>
                            <BottomNavigationAction className={classes.root}
                                style={{ padding: 0 }}
                                icon={<GitHub />}
                            />
                        </Button>
                    </a>

                    <a href='https://instagram.com/GeniusHawlah' target="_blank">
                        <Button className={classes.button}>
                            <BottomNavigationAction className={classes.root}
                                style={{ padding: 0 }}
                                icon={<Instagram />}
                            />
                        </Button>
                    </a>

                </ButtonGroup>
            </div>
            <Typography style={{ textDecorationLine: 'none', display: 'flex', justifyContent: 'center' }} variant='subtitle' >
                <a button className={classes.titleAndSubtitle} style={{ textDecorationLine: 'none', display: 'flex', justifyContent: 'center' }} href='mailto:geniusdecode@gmail.com'> {<Email />}geniusdecode@gmail.com</a> <br />
            </Typography>
            <Typography style={{ textDecorationLine: 'none', display: 'flex', justifyContent: 'center' }} variant='subtitle' >
                <a button className={classes.titleAndSubtitle} style={{ textDecorationLine: 'none', display: 'flex', justifyContent: 'center'}} href='https://wa.me/2347069426383?text=Hello%20GeniusHawlah,%20I%20will%20like%20to%20'>{<WhatsApp/>}WhatsApp</a>
            </Typography>
        </Box>

    );
};
