import React from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/styles";
import {
  Button,
  ButtonGroup,
  ListItemIcon,
  BottomNavigation,
  BottomNavigationAction,
  ListItem,
  List,
  Grid,
  Paper,
} from "@material-ui/core";
import {
  Person,
  Home,
  Description,
  Settings,
  AssignmentTurnedIn,
  Work,
} from "@material-ui/icons";
import "./FooterMobile";

import Facebook from "@material-ui/icons/Facebook";
import Twitter from "@material-ui/icons/Twitter";
import LinkedIn from "@material-ui/icons/LinkedIn";
import Instagram from "@material-ui/icons/Instagram";
import { Portfolio } from "./Portfolio";
import FooterMobile from "./FooterMobile";

const myStyle = makeStyles((theme) => ({
  root: {
    // flexGrow: 1,
    // display: 'flex',
    flexDirection: "row",
    alignItems: "spaceAround",
    "& > *": {
      // margin: theme.spacing(1),
    },
    //   MANIPULATION FOR THE ICONS
    "& .MuiBottomNavigationAction-root": {
      minWidth: 0,
      maxWidth: 250,
    },
    "& .MuiSvgIcon-root": {
      fill: "navy",

      // fontSize: '1.5rem',
      "&:hover": {
        fill: "blue",
        // fontSize: '2rem',
      },
    },
  },
  button: {
    margin: "1rem 2rem 1rem 2rem",
    color: "navy",
    // borderColor: 'skyBlue',
    // backgroundColor: 'white',
    "&:hover": {
      background: "white",
      fill: "navy",
    },
  },
}));

const Footer = () => {
  const classes = myStyle();

  return (
    <Grid
      container
      style={{
        background: "skyBlue",
        position: "fixed",
        bottom: "0",
        width: "100vw",
      }}
    >
      <Grid container justify="space-evenly">
        <Link to="/" style={{ textDecorationLine: "none" }}>
          <Button
            // variant="outlined"
            // color="primary"
            size="large"
            className={classes.button}
            startIcon={<Home />}
          >
            HOME
          </Button>
        </Link>

        <Link to="/skills" style={{ textDecorationLine: "none" }}>
          <Button
            // // variant="outlined"
            // color="primary"
            size="large"
            className={classes.button}
            startIcon={<Settings />}
          >
            Skills
          </Button>
        </Link>

        {/* <Link to='/profile' style={{ textDecorationLine: 'none' }} >
                        <Button
                            // variant="outlined"
                            // color="primary"
                            size="large"
                            className={classes.button}
                            startIcon={< Person />}
                        >
                            Profile
      </Button>
                    </Link> */}

        <Link to="/portfolio" style={{ textDecorationLine: "none" }}>
          <Button
            // // variant="outlined"
            // color="primary"
            size="large"
            className={classes.button}
            startIcon={<Work />}
          >
            PORTFOLIO
          </Button>
        </Link>

        {/* <Link to='/articles' style={{ textDecorationLine: 'none' }} > */}
        <Button
          onClick={() => {
            alert("Page under construction");
          }}
          // variant="outlined"
          // color="primary"
          size="large"
          className={classes.button}
          startIcon={<Description />}
        >
          ARTICLES
        </Button>
        {/* </Link> */}
      </Grid>
    </Grid>
  );
};

export default Footer;
