import React from 'react'
import { makeStyles, withStyles } from '@material-ui/styles'
import { TextField, Typography, Button, Grid, Box } from '@material-ui/core'
import SendIcon from '@material-ui/icons/Send'
import Navbar from './Navbar'


// makeStyles TO STYLE THE BUTTON
const myStyle = makeStyles((theme) => ({
    form: {
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        position: 'absolute'
    },
    button: {
        marginTop: '1rem',
        color: 'navy',
        // borderColor: 'navy',
        // backgroundColor: 'skyBlue',
        "&:hover": {
            background: 'skyBlue'
        }
        
        
    }
}));
// TO MANIPULATE THE InputField, WE NEEDED TO IMPORT THE withStyles
const InputField = withStyles({
    root: {
        '& label.Mui-focused': {
            color: 'navy'
        },
        '& label': {
            color: 'navy'
        },
        '& .MuiOutlinedInput-root': {
            '& fieldset': {
                borderColor: "navy"
            },
            '&:hover fieldset': {
                borderColor: 'skyBlue'
            },
            '&.Mui-focused fieldset': {
                borderColor: 'navy'
            }
        }
    },
})(TextField)

const Contacts = () => {
    const classes = myStyle();
    return (
        <Box component='div' style={{background:'white', height:'100vh'}}>
            {/* <Navbar /> */}
            <Grid container justify='center'>
                <Box component='form' className={classes.form}>
                    <Typography style={{color:'navy', textAlign:'center'}} variant='h5'>
                        EMAIL ME
                    </Typography>
                    <InputField fullWidth={true} label='Name' variant='outlined' margin='dense' size='medium' inputProps={{
                        style: { color: 'navy' }
                    }} />
                    <InputField fullWidth={true} label='Email' variant='outlined' margin='dense' size='medium' inputProps={{
                        style: { color: 'navy' }
                    }}/>
                    <InputField fullWidth={true} label='Name' variant='outlined' margin='dense' size='medium' inputProps={{
                        style: { color: 'navy' }
                    }} />
                    <br />
                    <Button className={classes.button} variant='outlined' fullWidth={true} endIcon={<SendIcon/>}>
                        Contact Me
                    </Button>
                </Box>
            </Grid>

        </Box>
    )
}

export default Contacts
