import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Box } from '@material-ui/core'
import Navbar from './Navbar'

const myStyles = makeStyles(theme => ({
    mainContainer: {
        background: 'white'
    },
    timeLine: {
        position: 'relative',
        padding: '1rem',
        margin: '0 auto',
        '&:before': {
            content: "''",
            position: 'absolute',
            height: '100%',
            border: '1px solid navy',
            right: '40px',
            top: '0'
        },
        '&:after': {
            content: "''",
            display: 'table',
            clear: 'both'
        },
        [theme.breakpoints.up('md')]: {
            padding: '2rem',
            '&:before': {
                left: 'calc(50% - 1px)',
                right: 'auto'
            }
        }
    },
    timeLineItems: {
        padding: '1rem',
        borderBottom: '2px solid navy',
        position: 'relative',
        margin: '1rem 3rem 1rem 1rem',
        clear: 'both',
        "&:after": {
            content: "''",
            position: 'absolute'
        },
        "&:before": {
            content: "''",
            position: 'absolute',
            right: '-0.625rem',
            top: 'calc(50% - 5px)',
            borderStyle: 'solid',
            borderColor: 'navy navy transparent transparent',
            borderWidth: '0.625rem',
            transform: 'rotate(45deg)'
        },
        [theme.breakpoints.up('md')]: {
            width: '44%',
            margin: '1rem',
            '&:nth-of-type(2n)': {
                float: 'right',
                margin: '1rem',
                borderColor: 'navy',
            },
            '&:nth-of-type(2n):before': {
                right: 'auto',
                left: '-0.625rem',
                borderColor: 'transparent transparent navy navy'
            }
        }
    },
    timeLineYear: {
        textAlign: 'center',
        maxWidth: '9.375rem',
        margin: '0 3rem 0 auto',
        fontSize: '1.8rem',
        backgroundColor: 'skyBlue',
        color: 'navy',
        lineHeight: 1,
        padding: "0.5rem 0 1rem",
        '&:before': {
            display: 'none'
        },
        [theme.breakpoints.up('md')]: {
            textAlign: 'center',
            margin: '0 auto',
            '&:nth-of-type(2n)': {
                float: 'none',
                margin: '0 auto'
            },
            '&:nth-of-type(2n):before': {
                display: 'none'
            }
        }
    },
    heading: {
        color: 'navy',
        backgroundColor: 'skyBlue',
        // margin: '5rem 0rem 0rem 0rem',
        padding: '3rem 0',
        textTransform: 'upperCase',

    },
    subHeading: {
        color: 'navy',
        padding: '0',
        textTransform: 'uppercase',
        fontWeight: "bolder",
        backgroundColor: 'skyBlue'

    }


}));

const Resume = () => {

    const classes = myStyles();

    return (
        <>
            <Navbar />
            <Box style={{
                margin: '-2.3rem 0rem 2.5rem 0rem',
            }} className={classes.mainContainer} component='header'>
                <Typography className={classes.heading} variant='h4' align='center'>THE BRIEF JOURNEY</Typography>
                <Box clas className={classes.timeLine} component='div'>
                    {/* YEAR ONE */}
                    <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>NOW</Typography>
                    <Box className={classes.timeLineItems} component='div'>
                        <Typography className={classes.subHeading} variant='h5' align='center'>
                            SIWES
                        </Typography>
                        <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                            Brookes Software Professional Services
                        </Typography>
                        <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                            I am observing a mandatory Students' Industrial Work Experience Scheme as a Frontend Developer at the above mentioned company.
                        </Typography>
                    </Box>
                    {/* YEAR 10 */}
                    <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>NOW</Typography>
                    <Box className={classes.timeLineItems} component='div'>
                        <Typography className={classes.subHeading} variant='h5' align='center'>
                            BSc. ICS
                        </Typography>
                        <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                            University of Ilorin
                        </Typography>
                        <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                            I am studying Information &amp; Communication Science; majorly, Software Development, till 2021.
                        </Typography>
                    </Box>
                    {/* YEAR 9 */}
                    <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>NOW</Typography>
                    <Box className={classes.timeLineItems} component='div'>
                        <Typography className={classes.subHeading} variant='h5' align='center'>
                            Remote Freelancing
                        </Typography>
                        <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                            Upwork Global Inc. (upwork.com)
                        </Typography>
                        <Typography style={{ color: 'navy' }} variant='subtitle1' align='justify'>
                            I work as a Freelance Content &amp; E-book Writer/Ghostwriter till date. You got an idea but don't have the time to pen it down in its best form? I am the guy who can brilliantly reproduce your pieces of ideas into a well-written content. <br /> <br />

                            I write on, but not limited to these niches:
                            Self Help, Mental Health, Psychology, Inspirational, Dating &amp; Relationship, Science &amp; Technology, Marketing, Football, Spirituality, Philosophy, Productivity, and Fictions.<br /> <br/>

                            I pick up your idea, convert it to a meaningful piece, timely deliver a 100% unique work, with no grammatical, spelling, or punctuation errors. <br /> <br />

                            I write ARTICLES on different fields applying my vast knowledge in Information Retrieval (IR) to make my research &mp; the end products outstanding, and lead in SEO results. I can also SPIN/REWRITE your already written articles to pass plagiarism test 100%, and make it original and unique.<br /> <br />

                            You already wrote a book but not sure if it's error-free? I can PROOFREAD when my service is needed. I carefully scrutinize the bundle of texts and make it error-free.

                            
                            I guess by now, you really want to hire me. <a  style={{ display: 'inline' }} href='https://wa.me/2347069426383?text=Hello%20GeniusHawlah,%20I%20will%20like%20to%20' target='_blank'> <button> Hit me up now,</button></a>  I'm ever ready to make you re-hire me. Thank you.
                        </Typography>
                    </Box>
                        {/* YEAR 8 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2015</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Personal Assistant / Clerk
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Yomi Oyenuga &amp; Co., Lighthouse Chambers Barristers &amp; Solicitors
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I worked as a personal assistant to the senior counsel till 2017.
                        </Typography>
                        </Box>
                        {/* YEAR 7 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2015</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Office Assistant
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Leo6 Technologies Limited
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I worked as an office assistant.
                        </Typography>
                        </Box>
                        {/* YEAR 6 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2014</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Classroom Teacher
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                FOLA Nursery and Primary Sch, Abule Oloni, Abeokuta
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I worked as a classroom teacher.
                        </Typography>
                        </Box>
                        {/* YEAR 5 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2014</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Diploma in Computer Science
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Olabisi Onabanjo University Consult, Abule Meko, Abeokuta, Ogun State
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I earned my Diploma Certificate in Computer Science and built a Command Line Interface CGPA Calculator with C++.
                        </Typography>
                        </Box>
                        {/* YEAR 4 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2012</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Industrial Work
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Latyf Vinegar &amp; Juice Industry, Sango Ota.
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I worked as a production assistant.
                        </Typography>
                        </Box>
                        {/* YEAR 3 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2012</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                Desktop Publishing
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Hyklas City Computer, Agosasa, Ipokia LG, Ogun State
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I earned a Desktop Publishing Certificate.
                        </Typography>
                        </Box>
                        {/* YEAR 2 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2011</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                SSC / CLASSROOM TEACHER
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Community High School, Agosasa, Ipokia LG, Ogun State
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I earned my West Africa Senior School Certificate and used it to work as a classroom teacher same year at De Light Nursery &amp; Primary School, Ibatefin.
                        </Typography>
                        </Box>
                        {/* YEAR 1 */}
                        <Typography className={`${classes.timeLineYear} ${classes.timeLineItems}`} variant='h2'>2005</Typography>
                        <Box className={classes.timeLineItems} component='div'>
                            <Typography className={classes.subHeading} variant='h5' align='center'>
                                FSLC
                        </Typography>
                            <Typography variant='body1' align='center' style={{ color: 'navy', fontWeight: 'bold' }}>
                                Nawairudeen Primary School, Agosasa, Ipokia LG, Ogun State.
                        </Typography>
                            <Typography style={{ color: 'navy' }} variant='subtitle1' align='center'>
                                I earned my First School Leaving Certificate.
                        </Typography>
                        </Box>
                    </Box>
            </Box>
            
        </>
    );
};
export default Resume;