import React from 'react';
import { createMuiTheme, makeStyles, ThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Navbar from './Navbar';

const myStyles = makeStyles(theme => ({
     root: {
         fontWeight: theme.typography.fontWeightBold,
        },
    
    styling: {
        background: 'white no-repeat center center fixed',
        height: '100vh',
        backgroundSize: 'cover',
        color: 'navy',

    },  
    heading: {
        color: 'navy',
        backgroundColor: 'skyBlue',
        // margin: '5rem 0rem 0rem 0rem',
        padding: '3rem 0',
        textTransform: 'upperCase',
        
    },
    
    
    }));

export default function Skills() {

    const component='container', classes = myStyles();

    return (
      <div
        style={{
          margin: "-2.3rem 0rem 2.5rem 0rem",
        }}
        className={classes.styling}
      >
        <Navbar />
        <Typography
          style={{
            paddingBottom: "0.5rem",
          }}
          className={classes.heading}
          variant="h5"
          align="center"
        >
          SKILLS
        </Typography>
        <ul>
          <li>
            <Typography className={classes.root} variant="h5">
              LANGUAGES &amp; WEB STACK
              <br />{" "}
              <span>
                <Typography variant="h6">
                  HTML, CSS, JS, C++, Java, Dart
                </Typography>
              </span>
            </Typography>
          </li>
          <br />
          <li>
            <Typography className={classes.root} variant="h5">
              TOOLS AND PACKAGES <br />{" "}
              <span>
                <Typography variant="h6">
                  Git/GitHub, VS Code, Android Studio, MS Ofﬁce Suite, Corel
                  Draw, Adobe Photoshop, Adobe XD, IntelliJ Idea.
                </Typography>
              </span>
            </Typography>
          </li>

          <br />
          <li>
            <Typography className={classes.root} variant="h5">
              LIBRARIES AND FRAMEWORKS <br />{" "}
              <span>
                <Typography variant="h6">
                  Bootstrap, Material UI, ReactJS, Flutter, JQuery
                </Typography>
              </span>
            </Typography>
          </li>
        </ul>
      </div>
    );
}
