import React from 'react';
import { createMuiTheme, makeStyles, ThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Navbar from './Navbar';

const myStyles = makeStyles(theme => ({
    root: {
        fontWeight: theme.typography.fontWeightBold,
    },

    styling: {
        background: 'white no-repeat center center fixed',
        height: '100vh',
        backgroundSize: 'cover',
        color: 'navy',

    },
    heading: {
        color: 'navy',
        // backgroundColor: 'skyBlue',
        // margin: '5rem 0rem 0rem 0rem',
        padding: '3rem 0',
        // textTransform: 'upperCase',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column'

    },


}));

export default function Error() {

    const component = 'container', classes = myStyles();

    return (
        <div style={{
            margin: '-2.3rem 0rem 2.5rem 0rem',

        }} className={classes.styling}>
            <Navbar />
           
            <ul>
                <li style={{ listStyleType: 'none',
                }} >
                    <Typography className={`${classes.root} ${classes.heading}`} style={{
                        paddingBottom: '0.5rem',

                    }} variant="h5">ERROR407<br /> <span><Typography variant='h6'>Invalid address</Typography></span></Typography>
                </li>
                <br />
                
            </ul>
        </div>
    );
}
